import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Logout = () => {
    const navigate = useNavigate();

    useEffect(() => {
        localStorage.removeItem('token'); // 토큰 제거
        alert('로그아웃 성공!');
        navigate('/'); // 메인 페이지로 이동
    }, [navigate]);

    return null;
};

export default Logout;
