import React from 'react';
import '../styles/procedure.css';

function Procedure() {
    return (
        <div className="adoption">
            <div className="preparation">
                <h2>입양 전 진지하게 점검해야할 마음가짐</h2>
                <div className="content">
                    <img src="https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog4.png?raw=true" alt="강아지와 아이" className="prep-image" />
                    <ul>
                        <li>반려동물을 맞이할 환경적 준비, 마음의 각오는 되어 있습니까?</li>
                        <li>환경이 바뀌더라도 한번 인연을 맺은 반려견을 끝까지 책임질 각오가 되어 있습니가?</li>
                        <li>모든 가족과는 합의가 되었습니까?</li>
                        <li>앞으로 함께 살아갈 반려견을 위해 공부할 각오가 되어있습니까?</li>
                        <li>아플 때 적절한 치료를 해수있습니까?</li>
                        <li>입양으로 인한 경제적 부담을 짊어질 의사와 능력이 있습니까?</li>
                        <li>우리 집에서 키우는 다른 동물과 잘 어울릴 수 있을지 고민해보았습니까?</li>
                    </ul>
                </div>
            </div>
            <div className="section-wrapper">
                <div className="section procedure">
                    <h1>입양 절차</h1>
                    <ol>
                        <li>
                            <h2>1. 입양 상담</h2>
                            <p>입양에 관심이 있는 분들은 먼저 입양 상담을 통해,<br /> 필요한 정보와 절차에 대해 안내받습니다.</p>
                        </li>
                        <li>
                            <h2>2. 신청서 제출</h2>
                            <p>입양을 원하시는 분들은 입양 신청서를 작성하여 제출합니다.</p>
                        </li>
                        <li>
                            <h2>3. 가정 방문</h2>
                            <p>입양 가능성을 평가하기 위해 가정 방문이 진행됩니다.<br /> 이를 통해 입양 환경을 평가합니다.</p>
                        </li>
                        <li>
                            <h2>4. 입양 승인</h2>
                            <p>가정 방문 결과를 토대로 입양 승인 여부가 결정됩니다.</p>
                        </li>
                        <li>
                            <h2>5. 입양 완료</h2>
                            <p>입양이 승인되면 최종 입양 절차가 진행되고,<br /> 아이를 맞이하게 됩니다.</p>
                        </li>
                    </ol>
                </div>
                <div className="section conditions">
                    <h1>입양 조건</h1>
                    <ol>
                        <li>
                            <h2>동의</h2>
                            <p>입양희망자의 가족 전구성원의 동의가 필요합니다.</p>
                        </li>
                        <li>
                            <h2>나이</h2>
                            <p>미성년자일 경우 부모님과 동행하여 동의가 필요합니다.</p>
                        </li>
                        <li>
                            <h2>동물 등록 정보 변경</h2>
                            <p>입양시 동물 등록 정보 변경은 필수입니다.</p>
                        </li>
                        <li>
                            <h2>책임감</h2>
                            <p>생활패턴이나 환경이 바뀌더라도<br /> 책임지고 잘 돌보아 줄 수 있는 보호자를 요합니다.</p>
                        </li>
                        <li>
                            <h2>1일 2산책</h2>
                            <p>산책은 강아지가 사람과 동물 다양한 환경에 상호작용하여<br /> 정신적 건강을 유지합니다.</p>
                        </li>
                    </ol>
                </div>
            </div>
        </div>
    );
}

export default Procedure;
